(function () {
    document.addEventListener('DOMContentLoaded', function () {
        var html = document.documentElement;
        var windowWidth = html.clientWidth;
        html.style.fontSize = windowWidth / 7.5 + 'px';
        if (windowWidth > 1024) {
            html.style.fontSize = 160 + 'px';
        }
    }, false);
})();