import Vue from 'vue'
import wechatAuth from './wechatAuth' //微信登录插件
import router from '@/router'
// import axios from 'axios'

// import store from '@/store/index'

const qs = require('qs');

router.beforeEach((to, from, next) => {
    console.log(to, 'to');
    console.log(from, 'from');

    let loginStatus = window.localStorage.loginStatus;
    // console.log(loginStatus, '登录状态');
    if (loginStatus == '0' || !loginStatus) {
        console.log('未登录');
        //微信未授权登录跳转到授权登录页面
        let url = window.location.href;
        //解决重复登录url添加重复的code与state问题
        let parseUrl = qs.parse(url.split('?')[1]);
        let loginUrl
        if (parseUrl.code && parseUrl.state) {
            delete parseUrl.code
            delete parseUrl.state
            loginUrl = `${url.split('?')[0]}?${qs.stringify(parseUrl)}`
        } else {
            loginUrl = url
        }
        wechatAuth.redirect_uri = loginUrl
        window.localStorage.loginStatus = "1";
        window.location.href = wechatAuth.authUrl
    } else if (loginStatus == '1') {
        console.log('微信授权登录返回');
        console.log(window.location.href, '地址');
        try {
            wechatAuth.returnFromWechat(window.location.href);
            next(to.path);
        } catch (err) {
            console.log(err, 'catch错误');
            window.localStorage.loginStatus = "0";
            next()
        }

    } else {
        console.log('登录成功');
        //授权成功后还需判断token是否过期
        let currentTimestamp = new Date().getTime();    //获取当前时间戳
        let expires = Number(window.localStorage.expires) || 0;
        console.log(new Date(expires).toLocaleString(), '过期时间');
        if (expires && (currentTimestamp > expires)) {
            window.localStorage.loginStatus = "0";
        } else {
            console.log('没有过期');
        }
        next()
    }
});

Vue.use(wechatAuth, {
    appid: 'wxca3e97c51546fc3e'
});

