import axios from 'axios'
// import router from '@/router/index'
import qs from 'qs'


// let HttpRequest = axios.create({
//     baseURL: "http://sdxddshg.app.xiaozhuschool.com/wxsite"
// });
//
// export default HttpRequest

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
    // 对响应数据做点什么
    if (response.data.code == 401) {
        window.localStorage.removeItem('token');
        window.localStorage.loginStatus = "0";
        window.location.reload();
    }
    return response;
}, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
});


const _Url = 'http://sdxddshg.app.xiaozhuschool.com/wxsite';

function getUrl(url) {
    /* 判断传入的url路径是否是http开头 */
    if (url.startsWith('http') || url.startsWith('https')) {
        return url
    }
    /* 不是http开头，进行路径的拼接 */
    return `${_Url}${url}`
}

const Http = {
    get: (url, query) => new Promise((resolve, reject) => {
        /* new Promise 是为进行在vue store里面进行异步传值 */
        var path = getUrl(url)
        /* get请求 */
        axios.get(path, {
            params: query
        }).then((res1, error) => {
            if (res1) {
                resolve(res1)
            } else {
                reject(error)
            }
        })
    }),

    post: (url, query, header = 'application/x-www-form-urlencoded') => new Promise((resolve, reject) => {
        /* new Promise 是为进行在vue store里面进行异步传值 */
        var path = getUrl(url)
        /* post请求 */
        axios({
            url: path,
            method: 'post',
            data: qs.stringify(query),
            headers: {
                'Content-Type': header
            }
        }).then((res1, error) => {
            if (res1) {
                resolve(res1)
            } else {
                reject(error)
            }
        })
    })
}

export default Http
