import Vue from 'vue'
import Router from 'vue-router'
import routes from './routers'

Vue.use(Router)
const router = new Router({
    routes,
    mode: 'hash'
})
router.beforeEach((to, from, next) => {
    //动态修改路由名称
    let routerName = null;
    if (to.meta.title) {
        routerName = to.meta.title;
        document.title = routerName;
    }

    console.log(to.name);
    // 新用户扫码必须注册绑定手机号码，授权开通免密支
    if (to.name === 'scan') {
        console.log('扫码页面');

    }

    next();
})

export default router

